import React from 'react'

import { RadioButton } from './components/RadioButton';
import { useArgs } from '@storybook/client-api';

export default {
    title: 'Components/RadioButton',
    component: RadioButton,
    argTypes: {
      isDisabled: {
        options: [true, false],
        control: {type: 'radio'},
        default: false,
      },
    },
};

const Template = (args) => {
  const [_, updateArgs] = useArgs();
  

  const handle = (e, f) => {
    console.log(e.target.value);
    const idx = args.find( ({ value }) => value === e.target.value );
    console.log(idx)
    args[0].isChecked = true
    updateArgs(args);
  };

  return (
  <form onChange={handle}>
    <RadioButton {...args[0]} />
    <RadioButton {...args[1]} />
  </form>
)};

export const Primary = Template.bind();
Primary.args = [{
  id: 0,
  groupName:"radioGroup",
  value:"button1",
  label:"Label 1",
  isDisabled:false,
  isChecked: false,
}, {
  id: 1,
  groupName:"radioGroup",
  value:"button2",
  label:"Label 2",
  isDisabled:false,
  isChecked: false,
}]


